import { Component, OnInit } from '@angular/core';
import { Location } from '@angular/common';
import { ResetService } from './reset.service';
import { Http, Response, Headers } from '@angular/http';
 import * as _ from "lodash";

declare var $:any;
@Component({
  selector: 'app-reset',
  templateUrl: './reset.component.html',
  styleUrls: ['./reset.component.css'],
  providers: [ ResetService ]
})
export class ResetComponent implements OnInit {

  public data;

  constructor(private resetservice: ResetService,private location: Location) {
  }

  ngOnInit() {

    this.checkFullPageBackgroundImage();

        setTimeout(function(){
            // after 1000 ms we add the class animated to the login/register card
            $('.card').removeClass('card-hidden');
        }, 700)
  }

  checkFullPageBackgroundImage(){
        var $page = $('.full-page');
        var image_src = $page.data('image');

        if(image_src !== undefined){
            var image_container = '<div class="full-page-background" style="background-image: url(' + image_src + ') "/>'
            $page.append(image_container);
        }
    };

     getUsers(){//

      $('button[type=submit]').attr('disabled', 'disabled');
      var email =  $("#username").val();
      var payno =  $("#payno").val();
      var idnumber =  $("#idnumber").val();

             this.resetservice.findUser().subscribe(
                data =>{
                   const str = data.json();
                  let user =<any>{};
                  user =  _.filter(str, { idNumber: idnumber, payNumber: payno, email: email });
                  console.log("find user", user);
                  if(user.length == 0){
                    alert("User does not exist");
                    $('input[type="text"]').val('');
                    $('button[type=submit]').removeAttr('disabled');
                    
                  }
                   else{
                    //for changing the password
                    
                      this.resetservice.userReset({"email":email})
                      .subscribe(
                          (response: Response)=> {
                            alert("Please Check your emails and click the link to reset your password"),
                              console.log("the response is:",response);
                             // this.clearFormData();
                             // this.closeNotifications();
                             this.location.back();
                         });


                    
                   }

              });
        }

 


}


