import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { LoginService } from './login.service';
import { Http, Response, Headers } from '@angular/http';

declare var $:any;

@Component({
    moduleId: module.id,
    selector: 'login-cmp',
    templateUrl: './login.component.html',
    providers: [LoginService ]
})

export class LoginComponent implements OnInit
{
    public siteurl = window.location.origin;
    public formModel: any = {};
    public loading = false;
    public returnUrl: string;



    constructor(private route: ActivatedRoute,private router: Router,private loginService: LoginService){}

    checkFullPageBackgroundImage(){
        var $page = $('.full-page');
        var image_src = $page.data('image');

        if(image_src !== undefined){
            var image_container = '<div class="full-page-background" style="background-image: url(' + image_src + ') "/>'
            $page.append(image_container);
        }
    };

    ngOnInit(){
        this.checkFullPageBackgroundImage();

        setTimeout(function(){
            // after 1000 ms we add the class animated to the login/register card
            $('.card').removeClass('card-hidden');
        }, 700);

        // reset login status
        this.loginService.logout();

         // get return url from route parameters or default to '/'
        this.returnUrl = this.route.snapshot.queryParams['returnUrl'] || '/dashboard';


    }

    login() {

        $('button[type=submit]').attr('disabled', 'disabled');
        this.loading = true;
        this.loginService.login(this.formModel.username, this.formModel.password)
            .subscribe(
                data => {
                    this.router.navigate([this.returnUrl]);
                    console.log("DataAuth",data);
                     console.log("this.returnUrl",this.returnUrl);
                     console.log("password",this.formModel.password);
                     sessionStorage.setItem('password', this.formModel.password);
                 },
                 error => {

                    $("#message").html("Username or Password incorrect")
                     this.loading = false;
                    $('button[type=submit]').removeAttr('disabled');
                 }
                );
    }

//     $("button").click(function() {
//     var $btn = $(this);
//     $btn.button('loading');
//     // simulating a timeout
//     setTimeout(function () {
//         $btn.button('reset');
//     }, 1000);
// });

}



