import { Injectable } from '@angular/core';
import { Http, Response, Headers, RequestOptions } from '@angular/http';
import { Observable } from 'rxjs/Observable';

@Injectable()
export class ResetService 
{
   constructor(private http: Http) {}  
   
     public findUser()  
     {     
          let url: string = 'https://tshwanesafetyapi.herokuapp.com/api/TshwaneUsers';
          return this.http.get(url);
        
    }
   
    public userReset(email: any)
    {
        let headers = new Headers({ 'Content-Type': 'application/json' });
        let options = new RequestOptions({ headers: headers });
        let body = JSON.stringify(email);

        return this.http.post('https://tshwanesafetyapi.herokuapp.com/api/TshwaneUsers/resetTshwaneUserPassword', body , options).map((res: Response) => res.json());

    }
    
}


